.parts-table {
   margin-top: rem(15);

   &-header {
      border-top: 2px solid $black;
      border-bottom: 2px solid $black;
      height: rem(43);
      display: flex;

      &-index {
         width: rem(50);
         background: $column-1;
         font-weight: 700;
         font-size: rem(18);
         line-height: rem(43);
         text-align: center;
         color: $black;
      }

      &-name {
         background: green;
         flex-grow: 1;
         background: $column-2;
         font-weight: 700;
         font-size: rem(18);
         line-height: rem(43);
         text-align: center;
         color: $black;
         display: flex;
         justify-content: center;

         & span {
            display: block;
         }
      }

      &-edit {
         width: rem(100);
         background: $column-1;
      }

      &-delete {
         width: rem(100);
         background: $column-2;
      }
   }

   &-body{
      border-bottom: 1px solid $black;
      height: rem(35);
      display: flex;

      &-index {
         width: rem(50);
         background: $column-1;
         font-weight: 400;
         font-size: rem(16);
         line-height: rem(35);
         text-align: center;
         color: $black;
      }

      &-name {
         background: $column-2;
         flex-grow: 1;
         font-weight: 400;
         font-size: rem(16);
         line-height: rem(35);
         text-align: start;
         padding-left: rem(30);
         color: $black;
      }

      &-edit {
         background: $column-1;
         width: rem(100);
         display: flex;
         justify-content: center;
         align-items: center;
         cursor: pointer;

         & img {
            width: rem(22);
            height: rem(22);
         }

         &:hover {
            background: $column-1-hover;
         }

         &:active {
            background: $column-1-active;
         }
      }

      &-delete {
         background: $column-2;
         width: rem(100);
         display: flex;
         justify-content: center;
         align-items: center;
         cursor: pointer;

         & img {
            width: rem(22);
            height: rem(22);
         }

         &:hover {
            background: $column-2-hover;
         }

         &:active {
            background: $column-2-active;
         }
      }
   }

   &-arrows {
      margin-left: rem(15);

      &-up {
         background: url('../img/icons/sort-arrow.svg') center (center / contain) no-repeat;
         display: block;
         width: rem(15);
         height: rem(12);
         margin-top: rem(9);
         cursor: pointer;

         &-active {
            background: url('../img/icons/sort-arrow-active.svg') center (center / contain) no-repeat;
         }
      }

      &-down {
         background: url('../img/icons/sort-arrow.svg') center (center / contain) no-repeat;
         transform: rotate(180deg);
         display: block;
         width: rem(15);
         height: rem(12);
         cursor: pointer;
         color: red;

         &-active {
            background: url('../img/icons/sort-arrow-active.svg') center (center / contain) no-repeat;
         }
      }
   }
}
